import * as React from "react"
import { useState } from "react"
import style from "../styles.css"
import Header from "../components/header"
import Footer from "../components/footer"
import ContentSection from "../components/content_section"
import SectionHeaderBlue from "../components/section_header_blue"
import isNarrowWindow from "../helpers/is_narrow_window"

const ContentTemplate = props => {
  const { pageContext } = props
  const { pageSections, pageTitle } = pageContext

  const [narrowWindow, setNarrowWindow] = useState(isNarrowWindow)

  if (typeof window === `undefined`) return(<></>)
  window.addEventListener('resize', () => setNarrowWindow(isNarrowWindow()))
  return (
    <main style={style}>
    <title>{pageTitle}</title>
  
    <Header narrowWindow={narrowWindow}/>
    <SectionHeaderBlue title={pageTitle} narrowWindow={narrowWindow}/>
    {
      pageSections.map ( (section, index) => {
        return <ContentSection title={section.title} content={section.content} narrowWindow={narrowWindow} />
      })
    }
    <Footer />
    </main>
  )
}
export default ContentTemplate