import * as React from "react"
import LeftBox from "../components/left_box"

/* SectionHeaderBlue is a variant of section header that's used on the content pages*/

const blockStyle = {
  display: "flex",
  alignItems: "center",
  flexGrow: 1,
  width: "100%",
  height: 85,
  borderBottomStyle: "solid",
  borderWidth: 1
}
const titleStyle = {
  fontFamily: "Mogena",
  fontSize: 25,
  fontWeight: 400,
  flexGrow: 1,
  flexShrink: 1,
  color: "#4F89D6",
  textTransform: "lowercase"
}

export default function SectionHeaderBlue(props) {
  return (
    <main style={blockStyle}>
      <LeftBox isHovered={true}/>
      {/* MORGENA DOESN'T HAVE PERIODS, REPLACE PERIODS WITH SPACE */}
      <h1 style={{
        ...titleStyle,
        ...{margin: props.narrowWindow ? "10px 0px 10px 20px" : "10px 0px 10px 105px"}
        }}>{props.title.replaceAll(".", "")}</h1>
    </main>
  )
}

